
@import url('https://fonts.googleapis.com/css2?family=Comfortaa:wght@300..700&display=swap');


*{
    box-sizing: border-box;
}
body{
    margin: 0;
    background-color: #F5F5F7;
    font-family: "karla",sans-serif;
    
    
}
html, body, #root {
    height: 100vh; 
  }




.header{
    display: flex;
    /* background-color: aqua; */
    height: 20vh;
    width: 100%;
    align-items: center;
    overflow-y: auto;
    font-family:'Courier New', Courier, monospace;
    
}
.headerin {
    margin-left: 7%;
    margin-right: 0;
    display: flex;
}





.righthead{
    display: flex;
    margin-left: auto;
    margin-right: 7%;
    overflow-y: auto;
    
}
.righthead > h3{
    padding-right: 40px;
    
}








.main{
    /* background-color: blueviolet; */
    display: flex;
    flex-direction: column;
    height: 100vh;
    overflow-y: auto;
    
    
}


.mainarea{
    width: 100%;
    height:100%; 
    /* background-color:blue ; */
    overflow-y: auto;
    
    
}



.intro{
    /* background-color:#fd87da; */
    display: flex;
    flex-direction: column;
    width: 85%;
    height: 80vh;
    margin-left: 10%;
    
    /* background-image: url('../public/images/introBack5AI.jpg');
    background-size: 700px 600px;
    background-repeat: no-repeat;
    background-position: right;
    overflow: auto; */
    
    
}
.intro > h1{
    font-family:'Courier New', Courier, monospace;
    margin-top: 13%;
    margin-left: 30%;
    font-size:30px;
}

.intro > h3{
    font-family:'Courier New', Courier, monospace;
    margin-top: 2%;
    margin-left: 36%;
    font-size:25px;
}

.exp{
    width: 85%;
    height: 70vh;
    margin-left: 10%;
    /* background-color: #0E1117; */
    
}

.expCardOuter{
    height: 35%;
    width: 90%;
    margin-top: 2%;
    margin-bottom:4%;
    /* background-color: #0E1117; */
    opacity: .9;
    border-radius: 10px;
    font-family: "Comfortaa", sans-serif;
    border: 2px solid #ccc;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: box-shadow 0.3s ease;
}

.expCardOuter:hover{
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

.expCardOuter > *{
    margin-left: 2%;
    margin-bottom: 1vh;
    margin-top: 1vh;
    
}
.expCardOuter>h3{
    font-size: 18px;
    color: blue;
    font-family:'Courier New', Courier, monospace;
}
.expCardOuter>h4{
    font-size: 16px;
    
}
.expDesc{
    font-weight:bold;
}

.contofExp{
    display: flex;
    margin-left: 10%;
    
    /* color:#F5F5F7 */
    /* background-color: blue; */
}
.exCards{
    /* background-color: aquamarine; */
    width: 100%;
    margin-top: 2.5%;
    
}

.vertical-line {
    display: flex;
    align-items: center;
  }
  .line {
    width: 2px; /* Adjust line thickness */
    opacity: .5;
    height: 50vh; /* Adjust line height */
    background-color: black; /* Line color */
    margin-right: 20px; /* Adjust distance between line and content */
  }


